'use client'

import { useEffect, useState } from 'react';

export default function useAuth() {
    const [isAuth, setIsAuth] = useState(false);

    useEffect(() => {
        if (typeof window !== 'undefined') {
            const token = localStorage.getItem('access_token');
            setIsAuth(!!token);
        }
    }, []);

    return isAuth;
}